*::-moz-selection {
    background: rgba(0, 255, 255, .25);
}

*::selection {
    background: rgba(0, 255, 255, .25);
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: inherit;
}

* {
  box-sizing: border-box;
}

.slider {
  background: #2B2B2B;
  width: 80%;
  height: 1px;
  margin: auto;
}

.thumb {
  transition: background .2s ease;
  position: relative;
  left: 12px;
  top: -20px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid #2B2B2B;
  background-color: #fff;
  cursor: pointer;
}

.thumb:hover {
  background: none;
}

.container_slider {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: space-around;
  margin: 100px 40px;
}
